import * as PIXI from "pixi.js";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { GameMediator } from "../Mediator/GameMediator";
import { Light } from "../Component/Light";
import { Firework } from "../Component/Firework";
import { Logo } from "../Component/Logo";
import { GameInfo } from "../Component/GameInfo";
import * as Tween from "gsap/TweenMax";
import { NextIssueInfo } from "../Component/NextIssueInfo";
import { PreNumber } from "../Component/PreNumber";
import { FrameGroup } from "../Component/FrameGroup";
import { SoundBtn } from "../Component/SoundBtn";
import { Issue } from "../Component/Issue";
import { Shap } from "../Component/Shap";

export class GamePanel extends PIXI.Container {

    get background2Index() {
        return this._background2Index;
    }

    set background2Index(v) {
        this._background2Index = v;
    }

    constructor(data) {
        super();

        this._multitonKey = data.key;
        this._stagewidth = data.width;
        this._stageheight = data.height;


        this._colors = [0x76F2FC, 0xFE79FF, 0x243BE4, 0xFFC938, 0xFE5671];

        this.once("added", this.onAddToStage);
    }

    onAddToStage() {
        this._background01 = new PIXI.Sprite();
        this.addChild(this._background01);
        this._background01_light = new PIXI.Sprite();
        this.addChild(this._background01_light);
        this._background02 = new PIXI.Sprite();
        this.addChild(this._background02);
        this._background02_light = new PIXI.Sprite();
        this.addChild(this._background02_light);

        this._topbackground = new Shap(0x000000, 0.2, this._stagewidth, this._stageheight * 0.1);
        this.addChild(this._topbackground);
        this._topbackground.visible = true;

        this._bottombackground = new PIXI.Sprite();
        this.addChild(this._bottombackground);


        this._lightComponent = new Light();
        this.addChild(this._lightComponent);
        this._light = new PIXI.Sprite();
        this.addChild(this._light);

        this._fireworkComponent = new Firework();
        this.addChild(this._fireworkComponent);

        this._Issue = new Issue();
        this.addChild(this._Issue);
        this._preNumberComponent = new PreNumber();
        this.addChild(this._preNumberComponent);

        // this._logoComponent = new Logo();
        // this.addChild(this._logoComponent);

        this._frameGroup = new FrameGroup();
        this.addChild(this._frameGroup);

        this._gameInfo = new GameInfo();
        this.addChild(this._gameInfo);

        this._soundbtnComponent = new SoundBtn();
        this.addChild(this._soundbtnComponent);

        this._slotMask = new PIXI.Graphics();
        this._slotMask.beginFill(0xffffff, 0);
        this._slotMask.drawRect(0, 0, 1125, 125);
        this._slotMask.endFill();
        this.addChild(this._slotMask);
        this._slotMask.pivot.x = this._slotMask.width * 0.5;
        this._slotMask.pivot.y = this._slotMask.height * 0.5;
        this._slotMask.position.set(this.StageWidth * 0.5, this.StageHeight * 0.68);

        this._flashMask = new PIXI.Sprite();
        this.addChild(this._flashMask);

        ApplicationFacade.getInstance(this._multitonKey).registerMediator(new GameMediator(this));
    }


    init(data) {
        this._background01.texture = PIXI.Texture.from(data.bg.bg01);

        this._background01_light.texture = PIXI.Texture.from(data.bg.bg01_light);
        this._background01_light.scale.set(2);
        this._background01_light.alpha = 0.5;
        this._background01_light.blendMode = PIXI.BLEND_MODES.ADD;
        this._background01_light.visible = true;

        this._background02.texture = PIXI.Texture.from(data.bg.bg02);
        this._background02.visible = false;

        this._background02_light.texture = PIXI.Texture.from(data.bg.bg02_light);
        this._background02_light.scale.set(2);
        this._background02_light.blendMode = PIXI.BLEND_MODES.ADD;
        this._background02_light.alpha = 0.5;
        this._background02_light.visible = false;

        this._lightComponent.init(data.lightbar);

        this._bottombackground.texture = PIXI.Texture.from(data.topbg);
        this._bottombackground.position.y = this._stageheight * 0.89;

        this._fireworkComponent.init(data.fireworks);
        this._fireworkComponent.Complete(() => this.allFireWorkComplete());

        // this._logoComponent.init(data.logo);
        this._Issue.init();

        this._preNumberComponent.init();
        this._preNumberComponent.position.y = this._stageheight * 0.89;

        this._frameGroup.init(data.frame);
        this._gameInfo.init(data.gameInfo);
        this._soundbtnComponent.init(data.sound);
        this._soundbtnComponent.scale.set(0.85);
        this._soundbtnComponent.position.set(this._stagewidth * 0.68, 2)


        this._light01BgTween = Tween.TweenMax.to(this._background01_light, 0.5,
            {
                // paused: true,
                alpha: 0,
                ease: Tween.Sine.easeInOut,
                repeat: -1,
                yoyo: true,
                yoyoEase: Tween.Sine.easeInOut,
            });

        this._light02BgTween = Tween.TweenMax.to(this._background02_light, 0.7,
            {
                paused: true,
                alpha: 0.2,
                ease: Tween.Sine.easeInOut,
                repeat: -1,
                yoyo: true,
                yoyoEase: Tween.Sine.easeInOut
            });

    }

    startSlot() {

        this._light01BgTween.paused(true);
        this._background01_light.visible = false;

        this._background02.alpha = 1;
        this._background02.visible = true;
        this._background02_light.alpha = 0.5;
        this._background02_light.visible = true;
        this._light02BgTween.paused(false);

        this._light.visible = true;
        this._lightComponent.play();
        // this._logoComponent.play();
        this._frameGroup.play();

        // this._flashMask.alpha = 0.5;
        // this._flashMask.visible = true;
        // this._flashTween1.play(0);

    }

    slotComplete() {
        this._lightComponent.reset();
        this._frameGroup.reset();

    }

    midComplete() {
        // this._flashMask.alpha = 1;
        // this._flashMask.visible = true;
        // this._flashTween2.play(0);
        this._light.visible = false;
        // this._logoComponent.reset();
        this._lightComponent.stop();
        this._fireworkComponent.play();
    }

    allFireWorkComplete() {
        Tween.TweenMax.to(this._background02, 0.5,
            {
                alpha: 0,
                onUpdate: () => {
                    this._background02_light.alpha = Math.min(this._background02.alpha, this._background02_light.alpha);
                },
                onComplete: () => {
                    this._background01_light.visible = true;
                    this._light01BgTween.paused(false);


                    this._background02.visible = false;
                    this._background02_light.visible = false;
                    this._light02BgTween.paused(true);
                }
            });
    }

    randMatrixColor(count) {
        let newfilter = new PIXI.filters.ColorMatrixFilter();
        newfilter.blendMode = PIXI.BLEND_MODES.ADD;

        newfilter.matrix[0] = Math.sin(count) * 3;
        newfilter.matrix[6] = Math.cos(count);
        newfilter.matrix[12] = Math.cos(count) * 1.5;
        /*newfilter.matrix[ 4 ] = Math.sin( count / 3 ) * 2;
        newfilter.matrix[ 5 ] = Math.sin( count / 2 );
        newfilter.matrix[ 6 ] = Math.sin( count / 4 );*/

        // console.log( newfilter );
        return newfilter;
    }


    colorMatrix(blendMode) {
        //let color = this.hexToRgb( hex );

        let result = new PIXI.filters.ColorMatrixFilter();
        result.blendMode = blendMode;
        result.matrix = [
            Math.random() * 255 / 255, 0, 0, 0, 0,
            0, Math.random() * 255 / 255, 0, 0, 0,
            0, 0, Math.random() * 255 / 255, 0, 0,
            0, 0, 0, 1, 0
        ];
        return result;
    }

    hexToRgb(hex) {
        let tmephex = hex;
        let result;
        if (Number.isInteger(tmephex)) {
            tmephex = "0x" + String.PadLeft(tmephex.toString(16), 6, "0").toUpperCase();
            result = /^0x?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec("" + tmephex);
        }
        else {
            console.log("2");
            result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec("" + tmephex);
        }

        return result ? {
            r: parseInt(result[1], 16),
            g: parseInt(result[2], 16),
            b: parseInt(result[3], 16)
        } : null;
    }

    get MultitonKey() {
        return this._multitonKey;
    }

    get StageWidth() {
        return this._stagewidth;
    }

    get StageHeight() {
        return this._stageheight;
    }

    get Soundbtn() {
        return this._soundbtnComponent;
    }

    get GameInfo() {
        return this._gameInfo;
    }

    get Issue() {
        return this._Issue;
    }

    get Firework() {
        return this._fireworkComponent;
    }

    get FrameGroup() {
        return this._frameGroup;
    }

    get PreNumber() {
        return this._preNumberComponent;
    }

}