import MVC from "puremvc";
import { GameProxyEvent } from "../../Event/GameEvent";

export class GameMediator extends MVC.Mediator {

    static get NAME() {
        return "GameMediator";
    }

    constructor(viewComponent) {
        super(GameMediator.NAME, viewComponent);
    }

    listNotificationInterests() {
        return [
            GameProxyEvent.INIT,
            GameProxyEvent.SETAUIDOBTN,
            GameProxyEvent.UPDATEGAMEINFO,
            GameProxyEvent.SETISSUE,
            GameProxyEvent.UPPRENUM,
            GameProxyEvent.UPPREISSUEINFO,
            GameProxyEvent.STARTSLIDER,
            GameProxyEvent.MIDCOMPLETE,
            GameProxyEvent.SLIDECOMPLETE,

            GameProxyEvent.FLINSHNUM,
            GameProxyEvent.FIREWORK,
            GameProxyEvent.FLASHMASK,
            GameProxyEvent.FRAMELIGHT,
        ];
    }

    handleNotification(notification) {
        switch (notification.getName()) {
            case GameProxyEvent.INIT:
                this.viewComponent.init(notification.getBody());
                break;
            case GameProxyEvent.SETAUIDOBTN:
                this.viewComponent.Soundbtn.trigger(notification.getBody());
                break;
            case GameProxyEvent.UPDATEGAMEINFO:
                this.viewComponent.GameInfo.upGameInfo(notification.getBody());
                break;
            case GameProxyEvent.SETISSUE:
                this.viewComponent.Issue.setIssue(notification.getBody());
                break;
            case GameProxyEvent.UPPRENUM:
                this.viewComponent.PreNumber.setNum(notification.getBody());
                this.viewComponent.FrameGroup.setPreNums(notification.getBody());
                break;
            case GameProxyEvent.UPPREISSUEINFO:
                // this.viewComponent.NextIssueInfo.updata(notification.getBody());
                break;
            case GameProxyEvent.STARTSLIDER:
                this.viewComponent.startSlot();
                break;
            case GameProxyEvent.MIDCOMPLETE:
                this.viewComponent.midComplete();
                break;
            case GameProxyEvent.SLIDECOMPLETE:
                this.viewComponent.slotComplete();
                break;
            case GameProxyEvent.FLINSHNUM:
                this.viewComponent.FrameGroup.setFinshNums(notification.getBody());
                break;
            case GameProxyEvent.FRAMELIGHT:
                this.viewComponent.FrameGroup.playlight(notification.getBody());
                break;
            default:
                break;
        }
    }
} 