import * as PIXI from "pixi.js";
import * as Tween from "gsap";
import "../../../Tools/Tool";

export class Light extends PIXI.Container {

    constructor() {
        super();

        this._colors = [0x0000FF, 0x75FE38, 0xEEFE38, 0xD25138, 0xD238FE];

        this._light01_1;
        this._light02_1;
        this._light03_1;
        this._light04_1;

        this._lights = [];
        this._lightTweens_In = [];
        this._lightTweens_Out = [];

        this._tweenAlpah = 1;
    }

    init(texture) {

        this._light01_1 = PIXI.Sprite.from(texture);
        //this._light01_1.filters = [this.colorMatrix(this._colors[4], PIXI.BLEND_MODES.ADD)];
        this.addChild(this._light01_1);
        this._light01_1.blendMode = PIXI.BLEND_MODES.ADD;
        this._light01_1.scale.set(0.5, 0.6);
        this._light01_1.anchor.set(0.5, 1);
        this._light01_1.rotation = -1;
        this._light01_1.x = 288;
        this._light01_1.y = 480;
        this._light01_1.visible = false;

        this._light02_1 = PIXI.Sprite.from(texture);
        //this._light02_1.filters = [this.colorMatrix(this._colors[0], PIXI.BLEND_MODES.ADD)];
        this.addChild(this._light02_1);
        this._light02_1.blendMode = PIXI.BLEND_MODES.ADD;
        this._light02_1.scale.set(0.5, 0.6);
        this._light02_1.anchor.set(0.5, 1);
        this._light02_1.rotation = 1;
        this._light02_1.x = 864;
        this._light02_1.y = 480;
        this._light02_1.visible = false;

        // this._light03_1 = new PIXI.Sprite(texture);
        // this._light03_1.filters = [this.colorMatrix(this._colors[3], PIXI.BLEND_MODES.ADD)];
        // this.addChild(this._light03_1);
        // this._light03_1.blendMode = PIXI.BLEND_MODES.ADD;
        // this._light03_1.anchor.set(0.5, 1);
        // this._light03_1.rotation = 1.55;
        // this._light03_1.x = 288;
        // this._light03_1.y = 145;
        // this._light03_1.visible = false;

        // this._light04_1 = new PIXI.Sprite(texture);
        // this._light04_1.filters = [this.colorMatrix(this._colors[2], PIXI.BLEND_MODES.ADD)];
        // this.addChild(this._light04_1);
        // this._light04_1.blendMode = PIXI.BLEND_MODES.ADD;
        // this._light04_1.anchor.set(0.5, 1);
        // this._light04_1.rotation = 1.55;
        // this._light04_1.x = 288;
        // this._light04_1.y = 145;
        // this._light04_1.visible = false;

        this._lights.push(this._light01_1, this._light02_1);

        this._lightTweens_In.push(
            Tween.TweenMax.to(this._light01_1, 1,
                {
                    paused: true,
                    rotation: 1,
                    //repeat: -1,
                    ease: Tween.Sine.easeInOut,
                    // yoy: true,
                    // yoyoEase: Tween.Sine.easeInOut,
                }),

            Tween.TweenMax.to(this._light02_1, 1,
                {
                    paused: true,
                    rotation: -1,
                    //repeat: -1,
                    ease: Tween.Sine.easeInOut,
                    // yoy: true,
                    // yoyoEase: Tween.Sine.easeInOut,
                }),

            // Tween.TweenMax.to(this._light03_1, 1.5,
            //     {
            //         paused: true,
            //         rotation: 1.3,
            //         repeat: -1,
            //         ease: Tween.Sine.easeInOut,
            //         yoy: true,
            //         yoyoEase: Tween.Sine.easeInOut,
            //     }),

            // Tween.TweenMax.to(this._light04_1, 1.5,
            //     {
            //         paused: true,
            //         rotation: 1.9,
            //         repeat: -1,
            //         ease: Tween.Sine.easeInOut,
            //         yoy: true,
            //         yoyoEase: Tween.Sine.easeInOut,
            //     })
        );


        this._lightTweens_Out.push(
            Tween.TweenMax.to(this._light01_1, 1,
                {
                    paused: true,
                    alpha: 0,
                    rotation: -1,
                    ease: Tween.Sine.easeInOut,
                }),

            Tween.TweenMax.to(this._light02_1, 1,
                {
                    paused: true,
                    alpha: 0,
                    rotation: 1,
                    ease: Tween.Sine.easeInOut,
                }),

            // Tween.TweenMax.to(this._light03_1, 1,
            //     {
            //         paused: true,
            //         alpha: 0,
            //         rotation: 1.55,
            //         ease: Tween.Sine.easeInOut,
            //     }),

            // Tween.TweenMax.to(this._light04_1, 1,
            //     {
            //         paused: true,
            //         alpha: 0,
            //         rotation: 1.55,
            //         ease: Tween.Sine.easeInOut,
            //     })
        );
    }

    reset() {
        for (let i = 0; i < this._lights.length; i++) {
            this._lights[i].alpha = 1;
            this._lights[i].visible = false;
            this._lightTweens_In[i].paused(true);
            this._lightTweens_Out[i].paused(true);
        }
    }

    play() {
        for (let i = 0; i < this._lights.length; i++) {
            this._lights[i].visible = true;
            this._lightTweens_In[i].play(0);
        }
    }

    stop() {

        for (let i = 0; i < this._lights.length; i++) {
            this._lights[i].visible = true;
            this._lightTweens_In[i].paused(true);
            this._lightTweens_Out[i].play(0);
        }
    }

    colorMatrix(hex, blendMode) {
        let color = this.hexToRgb(hex);

        let result = new PIXI.filters.ColorMatrixFilter();
        result.blendMode = blendMode;
        result.matrix = [
            color.r / 255, 0, 0, 0, 0,
            0, color.g / 255, 0, 0, 0,
            0, 0, color.b / 255, 0, 0,
            0, 0, 0, 1, 0
        ];
        return result;
    }

    hexToRgb(hex) {
        let tmephex = hex;
        let result;
        if (Number.isInteger(tmephex)) {
            tmephex = "0x" + String.PadLeft(tmephex.toString(16), 6, "0").toUpperCase();
            result = /^0x?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec("" + tmephex);
        }
        else {
            console.log("2");
            result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec("" + tmephex);
        }

        return result ? {
            r: parseInt(result[1], 16),
            g: parseInt(result[2], 16),
            b: parseInt(result[3], 16)
        } : null;
    }
}