import * as PIXI from "pixi.js";
import * as Tween from "gsap";

export class SlideVerticalGropu extends PIXI.Container {


    constructor() {
        super();

        this._realNum;
        this._slideNums_sp = [];

        this._realTweenOut;
        this._slideTweens = [];

        this.once("added", this.onAddToStage);
    }

    onAddToStage() {
        this._realNum = new PIXI.Sprite(this.parent.getRenderTexture());
        this._realNum.visible = false;
        this.addChild(this._realNum);
        this._realNum.anchor.set(0.5);
        this._realTweenOut = Tween.TweenMax.to(this._realNum, 1,
            {
                paused: true,
                alpha: 0,
                ease: Tween.Sine.easeOut,
                onComplete: () => {
                    for (let i = 0; i < this._slideTweens.length; i++) {
                        this._slideTweens[i].play();
                    }
                }
            })

        for (let i = 0; i < 5; i++) {
            let tempSlideSp = new PIXI.Sprite(this.parent.getRenderTexture());
            this.addChild(tempSlideSp);
            tempSlideSp.anchor.set(0.5);
            tempSlideSp.scale.set(0.1);
            tempSlideSp.visible = false;

            this._slideNums_sp.push(tempSlideSp);

            let tmepSliderTween = new Tween.TimelineMax(
                {
                    paused: true,
                    repeat: -1,
                    delay: 0.14 * i,
                });

            tmepSliderTween.add(Tween.TweenMax.to(tempSlideSp.scale, 0.2,
                {
                    x: 1,
                    y: 1,
                    onStart: () => {
                        tempSlideSp.texture = this.parent.getRenderTexture();
                        tempSlideSp.alpha = 1;
                        tempSlideSp.visible = true;
                    },
                }));
            tmepSliderTween.add(
                Tween.TweenMax.to(tempSlideSp, 0.5,
                    {
                        x: 75,
                        y: -75,
                        alpha: 0,
                        ease: Tween.Sine.easeOut,
                        onComplete: () => {
                            tempSlideSp.visible = false;
                        }
                    }));

            this._slideTweens.push(tmepSliderTween);
        }
    }

    reset() {

        this._realNum.visible = false;
    }

    play() {
        // this._realTweenOut.play(0);
        for (let i = 0; i < this._slideTweens.length; i++) {
            this._slideTweens[i].play();
        }
    }

    setFisnshRealNum(texture) {
        for (let i = 0; i < this._slideTweens.length; i++) {
            this._slideTweens[i].paused(true);
        }
        for (let i = 0; i < this._slideNums_sp.length; i++) {
            this._slideNums_sp[i].visible = false;
        }

        this._realNum.texture = texture;
        this._realNum.alpha = 1;
        this._realNum.visible = true;
    }

    setPreRealNum(texture) {
        this._realNum.texture = texture;
    }

}