import * as PIXI from "pixi.js";
import { SlideVerticalGropu } from "./SlideVerticalGropu";
import { TweenMax } from "gsap";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { GameCommandEvent } from "../../Event/GameEvent";
import { AudioCommandEvent } from "../../Event/AudioEvent";
import { AudioManifest } from "../../Mode/Data/AudioManifest";

export class FrameGroup extends PIXI.Container {

    constructor() {
        super();

        /**@type {[PIXI.extras.AnimatedSprite]} */
        this._frames = [];
        this._frameLights = [];
        //_frameBottomLights = [];
        this._frameTweens = [];

        this._slideVGP = [];

        this._numbers_tt = [];
    }

    init(data) {

        this._numbers_tt = []
        for (let i = 0; i < data.numbers.length; i++) {
            this._numbers_tt.push(PIXI.Texture.from(data.numbers[i]));
        }


        for (let i = 0; i < 5; i++) {
            let tempframe = PIXI.Sprite.from(data.frame);
            this.addChild(tempframe);
            tempframe.anchor.set(0.5);
            //tempframe.blendMode = PIXI.BLEND_MODES.ADD;
            tempframe.scale.set(1);
            tempframe.x = 160 + i * (tempframe.width);
            tempframe.y = this.parent.StageHeight * 0.75;
            //tempframe.animationSpeed = 0.5;
            this._frames.push(tempframe);

            let tempframelight = PIXI.Sprite.from(data.framelight);
            this.addChild(tempframelight);
            tempframelight.alpha = 0.5;
            tempframelight.anchor.set(0.5);
            tempframelight.blendMode = PIXI.BLEND_MODES.ADD;
            tempframelight.position.set(tempframe.x + 10, tempframe.y - 25);
            tempframelight.visible = false;
            this._frameLights.push(tempframelight);

            // let templight = new PIXI.Sprite(data["framelight"]);
            // this.addChild(templight);
            // templight.blendMode = PIXI.BLEND_MODES.ADD;
            // templight.anchor.set(0.5);
            // tempframelight.anchor.set(0.5);
            // templight.position.set(tempframe.x + 10, tempframe.y - 25);
            // templight.visible = false;
            // this._frameBottomLights.push(templight);

            this._frameTweens.push(
                TweenMax.to(tempframe, 1,
                    {
                        paused: true,
                        repeat: -1,
                        onUpdate: () => {
                            tempframe.rotation += 0.5;
                        }
                    }));



            let tempSVGP = new SlideVerticalGropu();
            this.addChild(tempSVGP);

            tempSVGP.position.set(tempframe.x, tempframe.y);
            this._slideVGP.push(tempSVGP);

        }
    }

    reset() {
        for (let i = 0; i < this._frameTweens.length; i++) {
            this._slideVGP[i].reset();
        }
    }

    play() {
        for (let i = 0; i < this._frameTweens.length; i++) {
            this._slideVGP[i].play();
        }
    }

    playlight(index) {
        if (index > this._frameLights.length) return;

        this._frameLights[index].alpha = 0.5;
        this._frameTweens[index].paused(true);
        this._frameLights[index].visible = true;
        TweenMax.to(this._frameLights[index], 2,
            {
                alpha: 0,
            });
    }

    setFinshNums(numbers) {
        if (numbers.length < 5) return;
        for (let i = 0; i < this._slideVGP.length; i++) {
            setTimeout(() => {
                this._slideVGP[i].setFisnshRealNum(this.getRenderTexture(numbers[i]));

                ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.NumberFlash });

                this.playlight(i);

                if (i === this._slideVGP.length - 1) {
                    ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(GameCommandEvent.SLIDECOMPLETE);
                }
            }, i * 500);
        }
    }

    setPreNums(numbers) {
        if (numbers.length < 5) return;
        for (let i = 0; i < this._slideVGP.length; i++) {
            this._slideVGP[i].setPreRealNum(this.getRenderTexture(numbers[i]));
        }
    }

    getRenderTexture(index = null) {
        let random = 0;
        if (index === null) {
            random = Math.floor(Math.random() * this._numbers_tt.length);
        }
        else {
            random = Math.min(index - 1, this._numbers_tt.length - 1);
        }

        return this._numbers_tt[random];
    }
}