import * as PIXI from "pixi.js";

export class PreNumber extends PIXI.Container {
    init() {
        this._num1 = new PIXI.extras.BitmapText("0", { font: '50px cumstomNum' })
        this.addChild(this._num1);
        this._num1.anchor.set(0.5);
        this._num1.position.set(427, 38);//395

        this._num2 = new PIXI.extras.BitmapText("0", { font: '50px cumstomNum' })
        this.addChild(this._num2);
        this._num2.anchor.set(0.5);
        this._num2.position.set(500, 38);

        this._num3 = new PIXI.extras.BitmapText("0", { font: '50px cumstomNum' })
        this.addChild(this._num3);
        this._num3.anchor.set(0.5);
        this._num3.position.set(573, 38);

        this._num4 = new PIXI.extras.BitmapText("0", { font: '50px cumstomNum' })
        this.addChild(this._num4);
        this._num4.anchor.set(0.5);
        this._num4.position.set(646, 38);

        this._num5 = new PIXI.extras.BitmapText("0", { font: '50px cumstomNum' })
        this.addChild(this._num5);
        this._num5.anchor.set(0.5);
        this._num5.position.set(719, 38);
    }

    setNum(numbers) {
        if (Array.isArray(numbers)) {
            if (numbers.length !== 5) return;

            this._num1.text = numbers[0];
            this._num2.text = numbers[1];
            this._num3.text = numbers[2];
            this._num4.text = numbers[3];
            this._num5.text = numbers[4];
        }
    }
} 